import { useLocation, useNavigate } from 'react-router-dom';
import './Advance.scss'



const AdvanceGuide = () => {
    const navigate = useNavigate();
    const location = useLocation();

    const fcolor = {
        color: '#EE334E',
        fontSize: '16px'
    }

    return (
        <div>
            <div className="page_nav_wrap">
                <div className="page_hoem">
                    <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home" 
                    onClick={()=>navigate('/')}/>
                </div>
                <div className="page_one">
                    <p onClick={()=>navigate('/advance_guide')}> </p>
                </div>
                <div className="page_list">
                    <p onClick={()=>navigate('/advance_guide')}> </p>
                </div>
            </div>
            <div className="common_wrap">

                <aside className='left'>
                    <div className="sidebar_wrap">
                        <div className="sidebar_title">
                            <p> </p>
                        </div>
                        <div className="sidebar_list">
                            <p className={location.pathname === '/advance_guide' ? 'active' : ''}
                                onClick={() => { navigate('/advance_guide') }}> </p>
                            <p className={location.pathname === '/advance' ? 'active' : ''}
                                onClick={() => { navigate('/advance'); }}> /</p>
                        </div>
                    </div>
                </aside>

                <aside className='right'>

                    <div className="bang_title">
                        <p>  </p>
                    </div>

                    <div className="bang_sub_title">
                        <p>  ?</p>
                        <p>           .<br />
                                        .</p>
                    </div>


                    <div className="bang_guide_wrap">

                        <div className="bang_guide">
                            <img src={process.env.PUBLIC_URL+"/img/bang1.png"} alt="advance" />
                            <div className="bang_guide_text">
                                <p>    ,   <br /> 
                                       </p>
                            </div>
                        </div>

                        <div className="bang_guide">

                            <div className="bang_guide_text">
                                <p> 
                                     <br /> 
                                       <br /> .</p>
                                <p style={fcolor}>(    <br />
                                      ex)12 1234) </p>
                            </div>
                            <img src={process.env.PUBLIC_URL+"/img/bang2.png"} alt="advance" />

                        </div>
                        <div className="bang_guide">
                            <img src={process.env.PUBLIC_URL+"/img/bang3.png"} alt="advance" />
                            <div className="bang_guide_text">
                                <p>       <br />
                                        </p>
                                <p style={fcolor}>   30   <br />  
                                       .
                                </p>
                            </div>
                        </div>
                    </div>
                </aside>
            </div>
        </div>


    )
}

export default AdvanceGuide