import { useLocation, useNavigate } from 'react-router-dom';
import './Advance.scss'


const Payment = () => {
  const { state } = useLocation();
  const navigate = useNavigate();
  const location = useLocation();


  const selectedCar = state?.car;
  const charge = state?.charge;

  const getUsageTime = (entry, exit) => {
    const start = new Date(entry);
    const end = new Date(exit);
    const diffMin = Math.floor((end - start) / 60000);
    const h = Math.floor(diffMin / 60);
    const m = diffMin % 60;
    return `${h} ${m}`;
  };

  const ver = {
    verticalAlign: 'bottom',
    paddingTop: '80px',
    fontWeight: 'bold'
  }

  return (

    <>
      <div className="page_nav_wrap">
        <div className="page_hoem">
          <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home"
            onClick={() => navigate('/')} />
        </div>
        <div className="page_one">
          <p onClick={() => navigate('/advance_guide')}> </p>
        </div>
        <div className="page_list">
          <p onClick={() => navigate('/advance')}> /</p>
        </div>
        <div className="page_list">
          <p onClick={() => navigate('/payment')}> </p>
        </div>

      </div>
      <div className="common_wrap">
        <aside className='left'>
          <div className="sidebar_wrap">
            <div className="sidebar_title">
              <p> </p>
            </div>
            <div className="sidebar_list">
              <p className={location.pathname === '/advance_guide' ? 'active' : ''}
                onClick={() => { navigate('/advance_guide') }}> </p>
              <p className={location.pathname === '/advance' ? 'active' : ''}
                onClick={() => { navigate('/advance'); }}> /</p>
            </div>
          </div>
        </aside>

        <aside className='right'>
          <div className="bang_title">
            <p> </p>
          </div>

          <div className="bang_pay_wrap">
            <table className='bang_pay_table'>
              <tbody>
                <tr>
                  <th><p> </p></th>
                  <td className='bang_check'>
                    <p>{selectedCar?.entry.split(' ')[0]}</p>
                    <p>{selectedCar?.location}</p>
                    <p>{selectedCar?.carNum}</p>
                  </td>
                  <td className='bang_sale'></td>
                  <td className='bang_pay' style={ver}>
                    <p>   {(charge?.parkingFee + charge?.evFee).toLocaleString()} </p>
                  </td>
                </tr>
                <tr>
                  <th></th>
                  <td className='bang_check'>
                    <p></p>
                    <p>  </p>
                    <p> </p>
                  </td>
                  <td className='bang_sale'></td>
                  <td className='bang_pay'>
                    <p>{selectedCar?.entry}</p>
                    <p>{charge ? getUsageTime(selectedCar.entry, selectedCar.exit) : '-'}</p>
                    <p>{selectedCar?.isEV && selectedCar?.chargeTime > 0 ? `${selectedCar.chargeTime}` : '  '}</p>
                  </td>
                </tr>
                <tr style={{ backgroundColor: '#D9D9D9' }}>
                  <th><p> </p></th>
                  <td className='bang_check'>
                    <p> </p>
                    <p> </p>
                    <p>  </p>
                  </td>
                  <td className='bang_sale'>
                    <p>{charge?.hasDiscount ? `${selectedCar?.discounts} 50% ` : ''}</p>
                  </td>
                  <td className='bang_pay'>
                    <p>{charge?.parkingFee.toLocaleString()} </p>
                    <p>{charge?.evFee.toLocaleString()} </p>
                    <p>{charge?.hasDiscount ? `- ${(charge.parkingFee + charge.evFee - charge.totalFee).toLocaleString()} ` : '0 '}</p>
                  </td>
                </tr>
              </tbody>
              <tfoot>
                <tr>
                  <th><p> </p></th>
                  <td className='bang_check'></td>
                  <td className='bang_sale'></td>
                  <th className='bang_pay'><p>{charge?.totalFee.toLocaleString()} </p></th>
                </tr>
              </tfoot>
            </table>

            <div className="bang_pay_btn">
              <button className='btn_1'> </button>
              <button className='btn_1'> </button>
            </div>
          </div>
        </aside>
      </div>
    </>

  );
};

export default Payment;
