import React, { useEffect, useState } from 'react'
import './OpenSpace.scss'
import { Accordion, Form, Navbar, Table, Button, Card, Pagination } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { useNavigate, useParams } from 'react-router-dom'
// import Pagination from 'react-bootstrap/Pagination';


const OpenSpace = () => {
    const { sub } = useParams();

    const [young_tab, setYoung_tab] = useState(1)
    const qna = useSelector(state => state.qna)
    const noticList = useSelector(state => state.notic)

    useEffect(() => {
        if (sub === 'guide') {
            setYoung_tab(0)
        } else if (sub === 'notic') {
            setYoung_tab(1)
        } else if (sub === 'qna') {
            setYoung_tab(2)
        }
    },[sub])

    //  UI 
    const [listOk, setListOk] = useState(true);  //  
    const [readOk, setReadOk] = useState(false);  //  
    const [boardInfo, setBoardInfo] = useState({});  //      (,  )  
    const tabNames = ['', '', '  (FAQ)'];
    const navigate = useNavigate();

    // 
    const [page, setPage] = useState([1, 1]);
    const [lastpage, setLastpage] = useState([1, 1]);

    let items = { 0: [], 1: [] };
    for (let i in page) {

        for (let number = 1; number <= lastpage[i]; number++) {
            items[i].push(
                <Pagination.Item key={number} active={number === page[i]}
                    onClick={() => handlePageChange(i, number)}>
                    {number}
                </Pagination.Item>,
            );
        }
    }

    //      
    const handlePageChange = (index, pageNumber) => {
        const newPage = [...page];
        newPage[index] = pageNumber;
        setPage(newPage);
    };

    useEffect(() => {
        const noticcount = Math.ceil(noticList.length / 5);
        const qnacount = Math.ceil(qna.length / 5);
        setLastpage([noticcount, qnacount]);
    }, [page])

    // FAQ
    const [activeKey, setActiveKey] = useState(null);
    const handleToggle = (eventKey) => {
        setActiveKey(prevKey => (prevKey === eventKey ? null : eventKey));
    };

    //   
    const boardListView = () => {
        setReadOk(false);
        setListOk(true);
    };

    //  
    const boardRead = (id) => {
        setListOk(false);
        setReadOk(true);
        const selectedBoard = noticList.find(notic => notic.id === id);
        // console.log(selectedBoard)
        setBoardInfo(selectedBoard);
    }

    return (

        <>

            <div className="page_nav_wrap">
                <div className="page_hoem">
                    <img src={process.env.PUBLIC_URL+"/img/home_icon.png"} alt="home" onClick={() => navigate('/')} />
                </div>
                <div className="page_one">
                    <p onClick={() => { setYoung_tab(0) }}> </p>
                </div>
                <div className="page_list">
                    <p>{tabNames[young_tab]}</p>
                </div>
            </div>

            <div className='common_wrap'>
                <div className="left">
                    <div className="sidebar_wrap">
                        <div className="sidebar_title">
                            <p></p>
                        </div>
                        <div className="sidebar_list">
                            <p className={young_tab === 0 ? 'active' : ''} onClick={() => {
                                setYoung_tab(0);
                                navigate('/open/guide')
                             }}></p>
                            <p className={young_tab === 1 ? 'active' : ''} onClick={() => {
                                setYoung_tab(1);
                                navigate('/open/notic')
                            }}></p>
                            <p className={young_tab === 2 ? 'active' : ''} onClick={() => {
                                 setYoung_tab(2)
                                 navigate('/open/qna') }}>  (FAQ)</p>
                        </div>
                    </div>
                </div>

                <div className="right">
                    {
                        young_tab === 1 ? (
                            <div className="young_notice">
                                <h2></h2>
                                {/*   */}
                                {listOk && (
                                    <>
                                        <Table striped bordered hover className='no'>
                                            <thead>
                                                <tr>
                                                    <th></th>
                                                    <th></th>
                                                    <th></th>
                                                    <th></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    noticList.slice((page[0] - 1) * 5, page[0] * 5).map((notic, i) =>
                                                        <tr key={i}>
                                                            <td><p>{notic.id + 1}</p></td>
                                                            <td onClick={() => boardRead(notic.id)}>{notic.title}</td>
                                                            <td>{notic.writer}</td>
                                                            <td>{notic.date}</td>
                                                        </tr>
                                                    )
                                                }
                                            </tbody>
                                        </Table>

                                        <Pagination size="sm">{items[0]}</Pagination>
                                    </>

                                )}


                                {/*   */}
                                {readOk && (
                                    <>
                                        <div className='text-end mb-3'>
                                            <button className='btn_1' onClick={boardListView}></button>
                                        </div>


                                        <div className="notic_body_title">
                                            <p>{boardInfo.title}</p>
                                        </div>

                                        <div className="notic_wrap">
                                            <table className='notic_body'>
                                                <tbody>
                                                    <tr>
                                                        <th></th>
                                                        <td>{boardInfo.writer}</td>
                                                    </tr>

                                                    <tr>
                                                        <th></th>
                                                        <td>{boardInfo.date}</td>
                                                    </tr>
                                                </tbody>

                                                <tfoot>
                                                    <tr>
                                                        <td colSpan={2}>
                                                            <img src={process.env.PUBLIC_URL+boardInfo.body} alt='img' width="100%" />
                                                        </td>
                                                    </tr>
                                                </tfoot>
                                            </table>

                                        </div>

                                        {/* <Card>
                                            <Card.Header>{boardInfo.title}</Card.Header>
                                            <Card.Body>
                                                <Card.Text><img src={boardInfo.body} alt='img' width="100%" /></Card.Text>
                                            </Card.Body>
                                        </Card> */}

                                    </>
                                    // <div className='mt-3'>
                                    //     <h5 className='text-start'>{boardInfo.title}</h5>
                                    //     <hr />
                                    //     <p className='text-start'>{boardInfo.description}</p>
                                    //     <div className='text-end mt-3'>
                                    //         <Button variant="secondary" onClick={boardListView}></Button>
                                    //     </div>
                                    // </div>
                                )}
                            </div>
                        ) : (
                            young_tab === 0 ? (
                                <div className="young_guide">
                                    <h1></h1>
                                    <ul>
                                        <li>  :        </li>
                                        <li>  :        </li>
                                    </ul>

                                      <div className='young_Sti'>
                                        <h3><a onClick={()=>{
                                            navigate('/month')
                                        }} style={{color:'#fff'}}> </a></h3>
                                    </div>

                                    <h2></h2>
                                    <ul>
                                        <li>      </li>
                                        <li> (695-0930)  365 24  <br />
                                            ) , ,      </li>
                                        <li> 1 , , ,            </li>
                                        <li>      </li>
                                        <li>    , (, , ) ,        </li>
                                        <li>   ,     </li>
                                    </ul>

                                    <h2>  </h2>
                                    <ul>
                                        <li> </li>
                                        <li>* (,  ) :      <br />
                                            * (, ,  ) :      <br />
                                            *         1 </li>
                                    </ul>
                                    <div className='young_img'>
                                        <img src={process.env.PUBLIC_URL+"/img/gammung.png"} alt="img" />
                                    </div>
                                    <ul>
                                        <li> (, , ,    )   <br />
                                                      </li>
                                    </ul>

                                    <ul>
                                        <li> <br />
                                            *       30 (   )     
                                             ,    <br />
                                              <br />
                                            *               <br />
                                            *         </li>
                                    </ul>
                                    <div className='young_img'>
                                        <img src={process.env.PUBLIC_URL+"/img/gammung2.png"} alt="img" />
                                    </div>


                                </div>
                            ) : (
                                <div className="young_faq">
                                    <h2>  (FAQ)</h2>
                                    <Table striped bordered hover className='no'>
                                        <thead>
                                            <tr>
                                                <th></th>
                                                {/* <th></th> */}
                                            </tr>
                                        </thead>
                                    </Table>
                                    <Accordion>
                                        {qna.slice((page[1] - 1) * 5, page[1] * 5).map((item, i) =>
                                            <Accordion.Item eventKey={i.toString()} key={i}>
                                                <Accordion.Header onClick={() => handleToggle(i.toString())}>
                                                    <span>Q.</span> {item.title}
                                                </Accordion.Header>

                                                <Accordion.Body>
                                                    <img src={process.env.PUBLIC_URL+item.body} alt={item.body} style={{ width: '100%', height: 'auto' }} />
                                                </Accordion.Body>
                                            </Accordion.Item>)}
                                    </Accordion>

                                    <Pagination size="sm">{items[1]}</Pagination>

                                </div>
                            )

                        )

                    }

                </div>
            </div>
        </>

    )
}

export default OpenSpace